import React, { Component } from 'react';
import '../css/showcaseItem.css';
import {Card, CardActions, CardHeader, CardMedia, CardTitle, CardText} from 'material-ui/Card';
import FlatButton from 'material-ui/FlatButton';
import FontAwesomeIcon from '@fortawesome/react-fontawesome'
import faAndroid from '@fortawesome/fontawesome-free-brands/faAndroid'
import faIos from '@fortawesome/fontawesome-free-brands/faAppStore'

const images = {
  avatar: require('../img/cl-min.jpg'),
  savant: {
    bg: require('../assets/showcase/savant/bg.png'),
  },
};

const styles = {
  cardMedia: {
    // width: 330,
    // height: 220,
  },
  avatar: {
    width: 36,
    height: 36,
    borderRadius: 5,
  },
};

class ShowcaseItem extends Component {
  state = {
  };

  renderIosLink() {
    const { app } = this.props;

    if (!app.iosLink) return null;

    // handle true vs string
    return (
      <a href={app.iosLink} target="_blank">
        <FontAwesomeIcon
          icon={faIos}
          size="2x"
          // color={brandColors.linkedIn}
          // style={{marginRight: 10, marginTop: 15, marginBottom: 15,}}
        />
      </a>
    );
  }

  renderAndroidLink() {
    const { app } = this.props;

    if (!app.androidLink) return null;

    // handle true vs string
    return (
      <a href={app.androidLink} target="_blank">
        <FontAwesomeIcon
          icon={faAndroid}
          size="2x"
          // color={brandColors.linkedIn}
          // style={{marginRight: 10, marginTop: 15, marginBottom: 15,}}
        />
      </a>
    );
  }

  render() {
    const { app } = this.props;

    return (
      <div className="col-sm-4">
        <Card
        >
          <CardHeader
            title={app.title}
            subtitle={app.subtitle}
            avatar={(
              <img className="app-icon-shadow" src={app.icon} alt="" />
            )}
          />
        {/*
          <CardMedia
          >
            <img src={images.savant.bg} alt="" style={styles.cardMedia}/>
          </CardMedia>
          */}
          <CardTitle title='' subtitle={app.website} />
          <CardText>
            {app.description}
          </CardText>
          <CardActions>
            {this.renderIosLink()}
            {this.renderAndroidLink()}
          </CardActions>
        </Card>
      </div>
    );
  }
}

export default ShowcaseItem;



