import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import Showcase from './components/showcase';
import Footer from './components/footer';
import About from './components/about';
import Recommendation from './components/recommendation';

const images = {
  avatar: require('./img/cl-min.jpg'),
};

class App extends Component {
  constructor(props) {
    super(props);

    this.navbarOffsetFromTop = null;
    this.state = {
      fixNav: false,
    };
  }

  onAppScroll = (event) => {
    if (this.navbarOffsetFromTop === null) return;

    const scrollTop = document.documentElement.scrollTop;
    if (scrollTop >= this.navbarOffsetFromTop) {
      this.setState({ fixNav: true })
    } else {
      this.setState({ fixNav: false })
    }
  }

  onAppMount = (node) => {
    if (!node) return;

    window.addEventListener('scroll', this.onAppScroll, true);
  }

  onNavMount = (node) => {
    if (!node) return;

    const position = node.getBoundingClientRect();
    this.navbarOffsetFromTop = position.top;
  }

  render() {
    return (
      <div
        ref={this.onAppMount}
        id="app"
        className={this.state.fixNav ? 'fix-navbar' : ''}
      >
        <div className="name-container" style={{height: 250, backgroundColor: '#e9ecef', marginBottom: 40, position: 'relative'}}>
          <div className="container">
            <h1 className="display-4">Chin Loong Tan</h1>
            <p className="lead">React Native Developer</p>
          </div>

        <div className="custom-nav"
          ref={this.onNavMount}
        >
          <nav className="navbar navbar-expand-lg navbar-light">
            <div className="collapse navbar-collapse" id="navbarSupportedContent">
              <ul className="navbar-nav mr-auto">
                <li className="nav-item active">
                  <a className="nav-link" href="#">
                    {false && 'Home <span className="sr-only">(current)</span>'}
                  </a>
                </li>
              </ul>
              <ul className="navbar-nav">
                <li className="nav-item">
                  <a className="nav-link" href="#">
                    Work
                  </a>
                </li>

                <li className="nav-item">
                  <a className="nav-link" href="#">
                    Blog
                  </a>
                </li>

                <li className="nav-item active">
                  <a className="nav-link" href="#">
                    Contact
                  </a>
                </li>

              </ul>
            </div>
          </nav>
        </div>
        </div>

        <About />

        <hr />

        <Showcase />

        <Recommendation />

        <Footer />
      </div>
    );
  }
}

export default App;



